}

.content {
  position: relative;
  width: 100%;
  height: 240px;
}

.content > div {
  position: absolute;
  width: 100%;
  height: 90px;
  overflow: visible;
  pointer-events: auto;
  -webkit-transform-origin: 50% 50% 0px;
  transform-origin: 50% 50% 0px;
  border-radius: 5px;
  color: white;
  line-height: 90px;
  padding-left: 32px;
  font-size: 16px;
  background: lightblue;
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: move;
}

.content > div:nth-child(1) {
